<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" >
<!--      顶部区域-->
      <div class="top">
        <img src="../../assets/image/login/back.png" alt="" class="back" @click="isback()">
      </div>
<!--      中间区域-->
      <div class="middle-box">
        <div class="main">
<!--          logo区域-->
          <div class="logo">
            <img src="../../assets/image/logo.png" alt="">
          </div>
<!--      表单区域-->
          <div class="form">
            <p class="logo-text" >注册</p>
              <div class="username-box">
                <img src="../../assets/image/login/user.png" alt="" style="margin-left:0">
                <input type="number" class="username" v-model="parameter.tel" placeholder="手机号 / 邮箱">
                <!-- <img src="../../assets/image/login/close.png" alt="" class="right-icon" style="margin-right:0"> -->
              </div>
              <div class="username-box">
                <img src="../../assets/image/login/password.png" alt="" style="margin-left:0">
                <input type="password" class="username" v-model="parameter.password" placeholder="请输入密码">
                <!-- <img src="../../assets/image/login/eye_close.png" alt="" class="right-icon" style="margin-right:0"> -->
              </div>
              <div class="username-box">
                <img src="../../assets/image/login/password.png" alt="" style="margin-left:0">
                <input type="number" class="username" v-model="validity.lockcode" placeholder="请输入短信验证码">
                <div class="userCode" @click="sendCode()">{{vcodeBtnName}}</div>
                <!-- <button @tap="sendCode" style="width: 260rpx; padding: 10rpx;font-size: 16rpx;">{{vcodeBtnName}}</button> -->
              </div>
              <div class="username-box">
                <img src="../../assets/image/login/password.png" alt="" style="margin-left:0">
                <!-- item.unfreeze == item.freeze_status? '完成': '解冻中' -->
                <!-- v-model="readonly?mask_car_no:car_no" -->
                <input type="number" class="username" v-model="code" placeholder="请输入邀请码">
                <!-- <img src="../../assets/image/login/eye_close.png" alt="" class="right-icon" style="margin-right:0"> -->
              </div>
          </div>
          <!--            登录按钮-->
          <div class="logo-btn" @click="userRegister()">注册</div>
        </div>
        <div class="footer">
          已有账号？ <span style="color: #079FDC" @click="tologin()">立即登录</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Checkbox, CheckboxGroup, Toast } from 'vant'
import { objKeySort, sgin } from '@/utils/auth'
import { anvitationcode, telcode, ajaxsms } from '@/api/user'
Vue.use(Checkbox).use(CheckboxGroup).use(Toast)
export default {
  components: {},
  data () {
    return {
      checked: true,
      vcodeBtnName: '获取验证码',
      parameter: {
        tel: '',
        password: '', // 密码
        code: ''
      },
      datas: {
        tel: ''
      },
      validity: {
        tel: '',
        lockcode: ''
      },
      verificationCode: '', // 短信验证码、
      countNum: '',
      code: '',
      curHeight: ''
    }
  },
  created () {
    // var data = objKeySort(this.parameter)
    // var token = sgin(data)
    // this.parameter.token = token
    // this.transactionList(this.parameter)
    this.beforeMount()
  },
  methods: {
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    isback () {
      this.$router.go(-1)
    },
    // 注册
    userRegister () {
      var that = this
      if (that.parameter.tel === '') {
        that.$toast({ message: '手机号不能为空', duration: 1500 })
        return
      }
      if (!(/^[0-9]{11}$/.test(that.parameter.tel))) {
        that.$toast({ message: '请输入正确手机号', duration: 1500 })
        return
      }
      if (that.parameter.password === '') {
        that.$toast({ message: '请输入密码', duration: 1500 })
        return
      }
      if (that.validity.lockcode === '') {
        that.$toast({ message: '请输入验证码', duration: 1500 })
        return
      }
      this.verifySms()
    },
    tologin () {
      this.$router.push({
        path: '/login',
        query: {
        }
      })
    },
    countDown () {
      if (this.countNum < 1) {
        clearInterval(this.countDownTimer)
        this.vcodeBtnName = '重新发送'
        return
      }
      this.countNum--
      this.vcodeBtnName = this.countNum + '秒重发'
    },
    verifySms () {
      var that = this
      that.validity.tel = that.parameter.tel
      var data = objKeySort(that.validity)
      var token = sgin(data)
      that.validity.token = token
      ajaxsms(that.validity).then(res => {
        console.log(res, '验证成功')
        if (res.code === 0) {
          if (that.code === '') {
            that.parameter.code = 0
          } else {
            that.parameter.code = that.code
          }
          var data = objKeySort(this.parameter)
          var token = sgin(data)
          this.parameter.token = token
          that.transactionList(this.parameter)
          delete this.validity.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete this.validity.token
      })
    },
    sendCode () {
      var that = this
      if (that.parameter.tel === '') {
        that.$toast({ message: '手机号不能为空', duration: 1500 })
        return
      }
      if (!(/^[0-9]{11}$/.test(that.parameter.tel))) {
        that.$toast({ message: '请输入正确手机号', duration: 1500 })
        return
      }
      if (that.vcodeBtnName !== '获取验证码' && that.vcodeBtnName !== '重新发送') { return }
      // that.$toast({ message: '短信已发送，请注意查收', duration: 1500 })
      that.countNum = 120
      that.countDownTimer = setInterval(function () { this.countDown() }.bind(this), 1000)
      // -----------
      that.datas.tel = that.parameter.tel
      var data = objKeySort(that.datas)
      var token = sgin(data)
      that.datas.token = token
      telcode(this.datas).then(res => {
        console.log(res, '发送短信1')
        if (res.code === 0) {
          that.$toast({ message: '短信已发送，请注意查收', duration: 1500 })
          delete this.datas.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        delete this.datas.token
      })
    },
    transactionList (parameter) {
      var that = this
      anvitationcode(parameter).then(res => {
        console.log(res, '注册成功')
        if (res.code === 0) {
          that.$toast({ message: '注册成功', duration: 1500 })
          setTimeout(() => {
            that.$router.go(-1)
          }, 1500)
          that.parameter.tel = ''
          that.parameter.password = ''
          that.validity.lockcode = ''
          that.parameter.code = 0
          delete this.parameter.token
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        that.parameter.tel = ''
        that.parameter.password = ''
        that.validity.lockcode = ''
        that.parameter.code = 0
        delete this.parameter.token
      })
    }
  }
}
</script>
<style scoped>
  .content{
    background: #12172B;
  }
  .film-box {
    position: relative;
  }

  .viewRecord {
    position: absolute;
    right: 2rem;
    top: 2rem;
    z-index: 20;
    background: #fff;
    border-radius: 100%;
  }

  .collection {
    position: absolute;
    right: 2.5rem;
    top: 2rem;
    z-index: 20;
    background: #fff;
    border-radius: 100%;
  }
  .top{
    height: 5rem;
    background: url("../../assets/image/login/bg.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  .back{
    width: 0.44rem;
    height: 0.44rem;
    /* float: left; */
    position: fixed;
    left: 0.62rem;
    top: 0.6rem;
    /* margin-top: 0.62rem; */
    /* margin-left: 0.3rem; */
  }
  .middle-box{
    background-color: #12172B;
  }
  .main{
    width:6.6rem;
    /* height:8.36rem; */
    background:rgba(27,33,59,1);
    box-shadow:0rem -0.1rem 0.2rem 0rem rgba(11,17,35,0.2);
    border-radius:0.3rem;
    margin: 0 auto;
    text-align: left;
    position: relative;
    /* top: -1.5rem; */
    top: -2.8rem;
    padding-bottom: 0.2rem;
  }
  .logo{
    width: 2rem;
    height: 2rem;
    margin: 0 auto;
    /* background:linear-gradient(0deg,rgba(16,99,255,1),rgba(2,197,255,1)); */
    border-radius:0.4rem;
    position: relative;
    top: -1rem;
  }
  .logo img{
    width: 100%;
    height: 100%;
  }
  .logo-text{
    margin-left: 0.48rem;
    color:rgba(255, 255, 255, 0.7);
    font-size: 0.36rem;
  }
  .form img{
    width: 0.4rem;
    height: 0.4rem;
    vertical-align: middle;
    margin-left: 0.43rem;
    margin-right: 0.3rem;
  }
  .right-icon{
    float: right;
    margin-right: 0.49rem;
  }
  .username-box{
    margin-top: 0.5rem;
    font-size: 0.28rem;
    color: #595C68;
    display: flex;
    padding-bottom:0.33rem;
    margin:  0.5rem 0.43rem 0 0.43rem;
    border-bottom: 0.02rem solid #13192E;
    position: relative;
  }
  .username{
    background: none;
    border: none;
    flex: 1;
  }
  .logo-btn{
    width:5.6rem;
    height:0.8rem;
    line-height: 0.8rem;
    text-align: center;
    background:linear-gradient(0deg,rgba(16,99,255,1),rgba(2,197,255,1));
    border-radius:0.1rem;
    /* color: white; */
    color: rgba(255, 255, 255, 0.7);
    margin: 0 auto;
    /* margin-top: 0.764rem; */
    margin-top: 0.5rem;
    margin-bottom: 0.3rem;
  }
  .footer{
    font-size: 0.24rem;
    z-index: 999999;
    position: relative;
    /* top: -1rem; */
    top: -2rem;
    text-align: center;
    color: #A4A5A7;
  }
  .userCode{
    position: absolute;
    right: 0rem;
    top: -0.1rem;
    padding: 0.1rem 0.2rem;
    line-height: 0.4rem;
    background: -webkit-linear-gradient(bottom,rgba(16,99,255,1),rgba(2,197,255,1));
    background: linear-gradient(0deg,rgba(16,99,255,1),rgba(2,197,255,1));
    border-radius: 0.22rem;
    color: rgba(255, 255, 255, 0.7);
  }
</style>
